export default {
  name: "w-loading",
	props:{
		text: String,
		mask: Number,
    click: Number,
	},
  data() {
    return {
      show: false,
      overText: ''
		};
  },
	methods:{
		Mclose(){
			if(this.click == 1){
				this.show = false
			}
		},
		open(){
			this.show = true
    },
    close(){
      this.show = false
    },
    over (overText) {
      this.overText = overText;
    }
	}
};
